*{
  font-family:"Roboto", sans-serif;
  box-sizing:border-box
}

.UNX-searchbox__container{
  display:inline-block;
  position:relative
}

.UNX-searchbox__button{
  margin:0.5rem
}

.UNX-searchbox__clearIcon{
  position:absolute
}

.UNX-products__list{
  display:grid;
  grid-gap:1rem;
  gap:1rem
}

.UNX-productCard__container .-details{
  width:100%;
  display:inline-block
}

.UNX-productCard__container .-image{
  height:200px;
  width:200px
}

.UNX-productCard__container .-price .-strike{
  text-decoration:line-through
}

.UNX-swatch__list{
  display:flex
}

.UNX-swatch__list .-image{
  height:30px;
  width:30px
}

.UNX-swatch__item{
  margin:0.25rem;
  cursor:pointer
}

.UNX-swatch__item.-selected{
  border-width:2px;
  --border-opacity:1;
  border-color:#718096;
  border-color:rgba(113, 128, 150, var(--border-opacity))
}

.UNX-facet__item{
  cursor:pointer;
  text-overflow:ellipsis;
  width:100%;
  position:relative;
  display:flex;
  align-items:center;
  margin:5px 0
}

.UNX-facet__item .-count{
  margin-left:5px
}

.UNX-facet__element{
  margin:10px 0
}

.UNX-facet__element .-clear{
  display:none;
  font-size:12px;
  padding:0;
  margin-left:auto
}

.UNX-facet__element .-input{
  border:1px solid #a7a7a7
}

.UNX-facet__element .-l1{
  padding-left:0
}

.UNX-facet__element .-l2{
  padding-left:12px
}

.UNX-facet__element .-l3{
  padding-left:24px
}

.UNX-facet__element .-l4{
  padding-left:36px
}

.UNX-facet__element.open .UNX-facet__list{
  max-height:500px;
  overflow:auto
}

.UNX-facet__element.open .UNX-facet__list.UNX-facet__listShowLimited{
  max-height:100px;
  overflow:auto
}

.UNX-facet__element.open .-clear{
  display:block
}

.UNX-facet__element.open .-collapse-icon:before{
  content:'-'
}

.UNX-facet__list{
  max-height:0;
  overflow:auto
}

.UNX-facet__header{
  font-size:17px
}

.UNX-facet__header .-collapse-icon{
  margin-left:10px
}

.UNX-facet__header .-collapse-icon:before{
  content:'+'
}

.UNX-facetActions__container .-apply{
  margin-right:5px
}

.UNX-facetActions__container .-clear{
  margin-left:5px
}

.UNX-spellCheck__item .-suggestion{
  margin-left:5px;
  text-decoration:underline
}

.UNX-pageNavigation__container{
  cursor:pointer
}

.UNX-pageNavigation__button{
  margin:0 5px
}

.UNX-sortby__container{
  display:flex
}

.UNX-pageSize__container{
  display:flex
}

.UNX-viewTypes__list{
  display:flex
}

.UNX-viewType__option{
  margin:0 5px
}

.UNX-breadcrumbs__list{
  display:flex;
  cursor:pointer
}

.UNX-breadcrumb__root,.UNX-breadcrumb__separator,.UNX-breadcrumbs-list-item{
  margin:0 2px
}

.UNX-selectedFacets__container{
  display:flex;
  align-items:center;
  cursor:pointer;
  margin-right:5px
}

.UNX-selectedFacets__list{
  display:flex
}

.UNX-selectedFacets__item{
  margin:0 5px
}

.UNX-loader__icon{
  height:100px;
  width:100px
}

.UNX-loader__icon path{
  fill:grey
}

.hidden{
  display:none
}

.-label{
  margin-bottom:0
}
